.gridForms{
    display: grid;
    grid-template-columns: repeat(2,1fr);
}
.panel{
    width: 90%;
    display: block;
    margin: 0 auto;
}

@media only screen and (max-width: 768px) {
   .gridForms {
       display: flex;
       flex-direction: column;
   }
   .panel {
       width: 100%;
   }
}